.tool-tip{
    border-radius: 5px;
    margin-top:-50px;
    visibility: hidden;
    font-size: 20px;
    background-color: #fafafa ;
    opacity: 0.7;

    width: 80%;
    z-index: 1;
    bottom: 100%;
    left: 50%;
    margin-left: -50%;

    font-weight: bold;
    text-align: center;
    position: absolute;
    
}



.tool-tip::after {
    content: " ";
    position:  absolute;
    top: 100%;
    left: 50%;
    
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #fafafa transparent transparent transparent;
  }



